<template>
    <div class="box">
        <div class="login">
            <router-link to="/login">登录</router-link>
        </div>
        <p> {{con}} </p>
        <mt-button type="default" @click="close">default</mt-button>
        <mt-button type="primary" @click="clicka">primary</mt-button>
        <mt-button type="danger" @click="add">danger</mt-button>
       
       <mt-actionsheet
        :actions="actions"
        v-model="sheetVisible">
        </mt-actionsheet>
       
        <router-view />
        <footer>
            <ul>
                <li>
                    <router-link to="/home/index">首页</router-link>
                </li>
                <li>
                    <router-link to="/home/video">视频</router-link>
                </li>
                <li>
                    <router-link to="/home/subscribe">订阅</router-link>
                </li>
                <li>
                    <router-link to="/user">个人</router-link>
                </li>
            </ul>
        </footer>
    </div>
</template>

<script>
import '@/style/reset.css';
import { Button } from 'mint-ui';
import { Toast } from 'mint-ui';
import { MessageBox } from 'mint-ui';
import { Actionsheet } from 'mint-ui';
export default {
    data(){
        return{
            k:'',
            con:"",
            actions:[
                {
                    name:'zl',
                    method:'callback'
                },
                {
                    name:'zzz',
                    method:'callback'
                }
            ],
            sheetVisible:""
        }
    },
    methods:{
        add(){
            this.k = Toast({
                    iconClass: 'fa fa-spinner fa-pulse',
                    position: 'middle',
                    duration: -1
                    });
        },
        close(){
            this.k.close()
        },
        clicka(){
            MessageBox.prompt('请输入姓名').then(({ value, action }) => {
                    this.con = value
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../style/var.scss';

 footer{
       position: fixed;
       bottom: 0;
        width: 100%;
        height:49/$sc+rem;
         line-height: 49/$sc+rem;
        background: #cfc;
        ul{
            display: flex;

        }
        li{
            font-size: 18px;
            flex:1;
            text-align: center;
            
        }
    }

    .box{
        font-size: 18px;
        .login{
             font-size: 26px;
            width: 100%;
            text-align:center
        }
    }
</style>



