<template>
    <div class="box">
       <!--  <router-view /> -->
        <swiper />
        <ul class="list" 
         v-infinite-scroll="loadMore"
         infinite-scroll-disabled="loading"
         infinite-scroll-distance="10">
            <li class="oLi" v-for="(item,index) in List.allLi">
               <a :href="item.link">
                   <div>
                        <h4>{{item.companyFullName}}
                            {{item.title}}</h4>
                        <p>
                            <span>{{item.companyName}}</span>
                            <span>
                                {{item.city}}
                                {{item.createTime}}</span>
                        </p>
                </div>
               <!--  <img :src="item.picInfo[0].url" alt=""> -->
              <img :src="'http://www.lgstatic.com/'+item.companyLogo" alt="">
              
               </a>
               
                <span class="del" @click="delList(index)">X</span>
            </li>
            
        </ul>
       
    </div>
</template>

<script>
import {mapActions,mapState,mapMutations} from 'vuex';
import '../../../node_modules/swiper/dist/css/swiper.css';
import Swiper from 'swiper'
import swiper from './swiper'
import { Toast,Indicator } from 'mint-ui';

export default {
    components:{
        swiper
    },
    data(){
        return {
          // allLi:[],
            arr:['A','B01','B02','B03','B04','B10'],
           // page:0,
            start:0,
            end:9
            //loading:false
        }
           
           
    },
    computed:{
        ...mapState(['List','allLi'])
        //console.log(allLi)
    },
    created() {
        //console.log(this.$jsonp)
        /* this.$jsonp("http://temp.163.com/special/00804KV1/post1603_api_all.js?_=1543633856800",{
            callbackName:'callback'
        }).then((res)=>{
           this.allLi = res.data
          console.log(res.data)
        }) */
        this.getData()
    },
    methods:{
        loadMore(){
            this.getData()
        },
        //获取
        ...mapActions(["getData"]),
        //删除
        ...mapMutations(['delList'])
       /*  getData(){
            if(this.page === this.arr.length-1){
                Toast({
                     message: '没有更多数据',
                    position: 'bottom',
                    duration: 2000
                });
                return
            }
            this.loading = true
           Indicator.open({
                text: '加载中...',
                spinnerType: 'double-bounce'
                });
                
            // this.$jsonp(`http://3g.163.com/touch/jsonp/sy/recommend/${this.start}-${this.end}.html`,{
            //   miss:'00',
            //   refresh:this.arr[this.page]
            // }).then((res)=>{
            //    // console.log(res.list)
            // this.allLi = this.allLi.concat(res.list.filter((item,index)=>{
            //     return item.picInfo[0]
            // }))
            // //console.log(res)
            // this.loading = false;
            // this.page++;
            // this.start = this.start+10;
            // this.end = this.start+9;
            // Indicator.close()
            // })
            this.$http.get("/lg/listmore.json",{
                params:{
                    pageNo:this.page,
                    pageSize:10
                }
            }).then((res)=>{
                console.log(res)
                this.allLi = this.allLi.concat(res.data.content.data.page.result)
                this.loading = false;
                this.page++
                Indicator.close()
                
            })
        }, */
        // del(idx){
         // var oLi = document.getElementsByClassName("oLi")
           // console.log(idx)
            //oLi[idx].style.display = "none"
           //List.allLi.splice(idx,1)
      //  } 

    }
}
</script>

<style lang="scss" scoped>
$sc:25;
 .list{
        width: 100%;
        li{
            box-sizing: border-box;
            width: 100%;
            height: 82/$sc+rem;
           
            padding: 4/$sc+rem 12/$sc+rem;
            div{
                float: left;
                width: 217.5/$sc+rem;
                height: 69.141/$sc+rem;
               
                h4{
                    height: 38/$sc+rem;
                   
                    font-size: 16px
                }
                p{
                    height: 29/$sc+rem;
                    line-height: 29/$sc+rem;
                    font-size: 13px;
                }
            }
            img{
               float: left;;
                height: 67/$sc+rem;
                width:110/$sc+rem;
            }
            .del{
                float: right;
                display: block;
                background: rgba(1,1,1,.4)
            }
        }
    }
</style>



