<template>
    <div class="box">
       <ul class="videoWrap">
           <li v-for="item in video">
               <p>
                   {{item.title}}
               </p>
               <video :src="item.video_url" 
               :poster="item.thumb2"
               controls="controls" preload="none"></video>
           </li>
       </ul>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            video:[]
        }
    },
    created() {
         var t = Toast({
                  
                    iconClass: 'fa fa-spinner fa-pulse',
                    position: 'middle',
                    duration: -1
                });
        this.$http.get("/wz/api/v3/get?_=1543633856801").then((res) => {
            console.log(res.data.data)
            this.video = res.data.data.filter((item,index)=>{
                return index >4
            })
             t.close()
          
        })
    },
}
</script>

<style lang="scss" scoped>
$sc:25;
.box{
    width: 100%;
    .videoWrap{
        width: 100%;
        li{
            width: 100%;
            video{
                width: 100%
            }
        }
    }
}

</style>



