export default{
    changeList(state,newList){
        state.allLi = state.allLi.concat(newList)
        //console.log(state.allLi)
    },
    changePage(state,p){
        state.page++
    },
    changeLoading(state,p){
        state.loading = p
    },
    delList(state,idx){
        state.allLi.splice(idx,1)
    }
}


