<template>
    <div class="box">
       <header>
           <router-link to="/home">
                <i class="fa fa-reply"></i>
          </router-link>
          <p>
              <i class="fa fa-search serIcon"></i>
              <input type="text" class="ser" ref="ser" />
          </p>
          <span>取消</span>
       </header>
        <section>
             
        </section>
        
    </div>
</template>

<script>

export default {
    methods:{
        
    }
}
</script>

<style lang="scss" scoped>
@import '../../style/var.scss';
.box{
    font-size: 18px
}
header{
    position: fixed;
    -webkit-z-index: 1;
    -moz-z-index: 1;
    -ms-z-index: 1;
    -o-z-index: 1;
    z-index: 1;
    top: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 44/$sc+rem;
    background:#cff;
    p{
        width: 225/$sc+rem;
        height: 25/$sc+rem;
        text-align: center;
        border: 1px solid #333;
        border-radius: (25/$sc)/2+rem;
    }
    .serIcon{
        display: none
    }
    .ser{
         width: 205/$sc+rem;
         height: 20/$sc+rem;
        border: none;
        outline: none
    }
}
section{
    overflow-y:auto; 
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;
    top: 0;
    background: #eee;
  
   padding-top: 44/$sc+rem;
}
</style>



