<template>
     <div id="nav">
            <div class="navleft">
                <div class="swiper-container2">
                    <div class="swiper-wrapper  w">
                        <div class="swiper-slide" 
                        v-for="(item,index) in navList"
                        :class="{'active':curIndex === index}"
                       >
                           {{item.title}}
                        </div>      
                    </div>
                    
                </div>
            </div>
            <div class="navright"><i class="fa fa-navicon"></i></div>
        </div>
</template>

<script>
export default {
     data(){
        return{
             allLi:[],
            curIndex:0,
            navList:[
                {
                    title:"aaa",
                    type:""
                },
                {
                    title:"bbb",
                    type:"pcent"
                },
                {
                    title:"ccc",
                    type:"pctech"
                },
                {
                    title:"ddd",
                    type:"t"
                },
                {
                    title:"科技5",
                    type:"k"
                },
                {
                    title:"头条6",
                    type:"t"
                },
                {
                    title:"科技1",
                    type:"k"
                },
                {
                    title:"头条2",
                    type:"t"
                },
                {
                    title:"科技3",
                    type:"k"
                },
                {
                    title:"头条4",
                    type:"t"
                },
                {
                    title:"科技5",
                    type:"k"
                },
                {
                    title:"头条6",
                    type:"t"
                }


            ]
        }
    },
     mounted() {
        var vm = this
        new Swiper(".swiper-container2",{
            freeMode:true,
            slidesPerView:'auto',
            on:{
                click(){
                    
                    vm.curIndex = this.clickedIndex
                    this.setTransition(200)
                   // console.log(this.clickedIndex)
                    var el = this.slides[this.clickedIndex]
                    //console.log(el)
                    //console.log(this.slides[this.clickedIndex])
                    //console.log(el.offsetLeft)
                    var elpos = el.offsetLeft + el.clientWidth/2
                    //console.log(elpos)
                    var fartherwidth = this.wrapperEl.clientWidth
                    //console.log(fartherwidth)
                    var maxDis = this.maxTranslate()
                   //console.log(fartherwidth,maxDis)
                    var maxPos = -maxDis + fartherwidth/2;
                    //console.log(maxPos)
                  //console.log(fartherwidth/2 - elpos)
                    if(elpos < fartherwidth/2){
                       this.setTranslate(0)
                    }else if(elpos >maxPos){
                        this.setTranslate(maxPos)
                    }else{
                         this.setTranslate(fartherwidth/2 - elpos)
                    };
                  
                     vm.$router.push("/home/index/"+vm.navList[vm.curIndex].type)
                  
                }
            }
        })
    },

}
</script>

<style lang="scss" scoped>
$sc:25;
    #nav{
        display: flex;
        margin-left:10/$sc+rem; 
        .navleft{
            width: 90%;
            overflow: hidden
        }
        .w {
             height: 20px
        }
        .w div{
            width: 70px;
            height: 20px;
            text-align: center
        }
    }
    .active{
        color:orange;
        border-bottom: 2px solid orange
    }
</style>



