<template>
    <div class="box">
       <header>
          
            <div class="headTop">
                <router-link to="/search">
                    <i class="fa fa-search right"></i>
                </router-link>
            </div>
          <router-view name="Nav" />
      
        </header>
        <router-view class="setion" />
        <footer>
            <ul>
                <li>
                    <router-link to="/home/index">首页</router-link>
                </li>
                <li>
                    <router-link to="/home/video">视频</router-link>
                </li>
                <li>
                    <router-link to="/home/subscribe">订阅</router-link>
                </li>
                <li>
                    <router-link to="/user">个人</router-link>
                </li>
            </ul>
        </footer>
    </div>
</template>

<script>
import '@/style/reset.css';
import Nav from './nav';
export default {
    components:{
        Nav
    }
}
</script>

<style lang="scss" scoped>
/* @import '../../style/reset.css'; */
@import '../../style/var.scss';
/*     $c:red;
    .bold{
        font-weight: 900
    }
    @mixin ct($ct:center){
        text-align: $ct
    }
    div{
        color: $c;
        font-size:.1rem;
        &>a{
            color: $c
        }
        @extend .bold;
        @include ct(left);
        background: #fcc;
        height: 2rem;
    } */


    .box{
        font-size:18px 
    }
    header{
        position: fixed;
        width:100%;
        height: 75/$sc+rem;
        background: #fff;
        top: 0;
        .right{
            float:right;
            margin:.5rem .5rem 0 0;
        };
       .headTop{
           height: 40/$sc+rem;
       }
    }
    .setion{
        width: 100%;
        position: absolute;
        top: 75/$sc+rem;
        right: 0;
        left: 0;
        bottom:  49/$sc+rem;
        background: #eee;
        overflow-y:auto 
    }
    footer{
       position: fixed;
       bottom: 0;
        width: 100%;
        height:49/$sc+rem;
         line-height: 49/$sc+rem;
        background: #fff;
        ul{
            display: flex;

        }
        li{
            font-size: 18px;
            flex:1;
            text-align: center;
            
        }
    }
</style>



