<template>
   <div class="box">
        <div class="top">
            <router-link to="/user">
                <i class="fa fa-reply"></i>
            </router-link>
            <span>
                <router-link to="">手机注册</router-link>
            </span>
        </div>
        <section>
            <ul>
                <li class="one">
                    <input v-model="phone" type="text" placeholder="请输入手机号" />
                    <div class="getcode" v-show="flag" @click="none">
                        获取验证码
                    </div>
                    <div  ref="codeAdd" class="codeAdd" v-show="!flag" @click="getValidate">
                       
                    </div>
                </li>
                <li><input v-model="cold" type="text" placeholder="请输入验证码" /></li>
                <li><input v-model="password"  type="text" placeholder="6-12位数字、字母、字符" /></li>
                <li><input v-model="passwordok" type="text" placeholder="确认密码" /></li>
            </ul>
            <input type="submit" class="complete" value="完成"
                @click="submit"
            >
                
           
        </section>
    </div>
</template>

<script>
    //
    //设置随机数字
    function randNum(n,m){
        if(n > m){
            var t = n;
            n = m;
            m = t;
        }
        return Math.round(Math.random()*(m-n)+n)
    }
    function getproNum(){
        var arr = [];
        while(arr.length < 4){
            var val = randNum(0,9)
            if(arr.indexOf(val) == -1){
                arr.push(val)
            }
        }
        return arr
    }
/* 
     function getproNum(){
        var arr = [];
        while(arr.length < 4){
            var val = randNum(65,90)
            if(arr.indexOf(val) == -1){
                arr.push(String.fromCharCode(val))
            }
        }
        return arr
    } 
    function arrfn(){
         var str = ''
        while(str.length < 4){
            var index = parseInt(Math.random()*30)
           str += ary[index]
        }
        return str
    }
*/
import '@/style/reset.css';
export default {
    data(){
        return{
            flag:true,
            cold:"",
            phone:"",
            password:"",
            passwordok:""
        }
    },
    methods:{
        none(){
            this.flag = false;
            this.$refs.codeAdd.innerHTML = getproNum().join("") 
        },
        getValidate(){
           parseInt(Math.random()*9)
           //console.log( this.$refs.codeAdd)
           this.$refs.codeAdd.innerHTML = getproNum().join("") 
        },
        submit(){
            //console.log(this.$http)
            var reg = /\w{6,12}/;
           if(this.$refs.codeAdd.innerHTML !== this.cold ||this.$refs.codeAdd.innerHTML === "" ){
                alert("验证码有误")
                return false
           }
           else if(!reg.test(this.password)){
               alert("请输入符合条件的密码")
           }
           else if(this.password !== this.passwordok){
                alert("密码不一致")
           }
           else{
               this.$http.post("http://localhost:3000/list",{
                   phone:this.phone,
                   password:this.password
               },{
                headers:{
                "content-type":"application/json"
                }
            }).then((res)=>{
                this.$router.push("/user")
            })
           }
        }
    }
}

</script>

<style lang="scss">
$sc:25;
.box{
    height: 100%;
    background: #eee;
    width: 100%;
    font-size: 18px;
    .right{
        float:right;
    };
    .top{
        width: 100%;
        height: 43.98/$sc+rem;
        line-height: 43.98/$sc+rem;
        background: #fff;
        border: 1px solid #ccc;

    };
    section{
        height: 441/$sc+rem;
        width: 100%;
         background: #eee;
         input{
              text-indent: 20px;
         };
         ul{
             margin-top: 40/$sc+rem;
             width: 100%;
             li{
                 background: #fff;
                 width: 100%;
                 height: 50.5/$sc+rem;
                 border-top: 1px solid #ccc;
                 line-height:  50.5/$sc+rem;
             };
             .one input{
                
                 width: 170/$sc+rem;
                
             };
             .one{
                 position: relative;
                   .codeAdd,.getcode{
                      float: right;
                     width: 97.5/$sc+rem;
                     height: 100%;
                    background: #fcc;
                    text-align: center;
                    opacity: .8;
                 };
                 .codeAdd{
                     position: absolute;
                     top:0;
                     right: 0;
                     background: #ccf
                 }
             }
         };
         .complete{
            height: 39/$sc+rem;
            width: 349/$sc+rem;
            background: #fff;
            margin: 114/$sc+rem 0 0 12.5/$sc+rem;
            border: 1px solid #ccc;
            text-align: center;
            line-height:  39/$sc+rem;
         }
    };
}

</style>



