<template>
   <div class="box" v-scroll="vis" dis="300" v-gd>
        <ul class="list">
            <li class="oLi" v-for="(item,index) in allLi">
               <a :href="item.url">
                   <div>
                        <h4>{{item.mintro}}</h4>
                        <p>
                            <span>{{item.source}}</span>
                            <span>{{item.ptime}}</span>
                        </p>
                </div>
              <img :src="item.thumb" alt="">
               </a>
               
                <span class="del" @click="del(index)">X</span>
            </li>
        </ul>
         <p class="btn" v-show="vis.falg" v-top>回到顶部</p>
   </div>
</template>

<script>
import '../../module/module'
export default {
    data(){
        return {
            type:'pcent',
            allLi:[],
            vis:{
                falg:false
            }
        }
    },
    created() {
        this.getData()
    },
    methods:{
        //console.log()
        getData(){
           // console.log(1)
            this.$jsonp(`http://cre.mix.sina.com.cn/api/v3/get?cateid=1&cre=tianyi&mod=${this.type}&merge=3&statics=1&length=20&up=0&down=0&fields=media&_=1543819418969`).then((res)=>{
                //console.log(res)
                this.allLi = res.data
            })
        },
        del(idx){
          //var oLi = document.getElementsByClassName("oLi")
           // console.log(idx)
            //oLi[idx].style.display = "none"
            this.allLi.splice(idx,1)
        }
    },
    watch:{
        $route:{
            handler(n){
   	  			this.type=n.params.type;
                     this.getData()
                     console.log(this.$router)
                 },
                 immediate:true
        }
    }
}
</script>

<style lang="scss" scoped>

$sc:25;
.box{
    position: fixed;
    top:75/$sc+rem;
    left:0;
    right:0;
    bottom: 50/$sc+rem;
    .btn{
        width: 50/$sc+rem;
        height:50/$sc+rem;
         position: fixed;
          bottom: 50/$sc+rem;
           right:50/$sc+rem;
           background: #fcc
    }
}
.list{
        width: 100%;
        li{
            box-sizing: border-box;
            width: 100%;
            height: 82/$sc+rem;
           
            padding: 4/$sc+rem 12/$sc+rem;
            div{
                float: left;
                width: 217.5/$sc+rem;
                height: 69.141/$sc+rem;
               
                h4{
                    height: 38/$sc+rem;
                   
                    font-size: 16px
                }
                p{
                    height: 29/$sc+rem;
                    line-height: 29/$sc+rem;
                    font-size: 13px;
                   
                    
                }
            }
            img{
               float: left;;
                height: 67/$sc+rem;
                width:110/$sc+rem;
            }
            .del{
                float: right;
                display: block;
                background: rgba(1,1,1,.4)
            }
        }
    }
</style>



