import Vue from 'vue'
Vue.directive("scroll",(el,binding)=>{
    var dis = parseInt(el.getAttribute("dis"))
    //console.log(dis)
    el.addEventListener("scroll",()=>{
       // console.log(binding)
        if(el.scrollTop > dis){
            binding.value.falg = true;

        }else{
            binding.value.falg = false;
        }
    })
})
Vue.directive("top",(el,binding)=>{
    el.onclick = function(){
        el.parentNode.scrollTop = 0
    }
})
Vue.directive("gd",{
    bind(el){
        var dis = parseInt(el.getAttribute("dis"))
        //console.log(dis)
        var flag = false
        var  timer = null
        el.addEventListener("scroll",()=>{
            //console.log(binding.value.falg)
            if(el.scrollTop > dis){
               console.log("dayu")
                if(flag) return;
                flag = true
               /*  if(timer){
                    clearInterval(timer)
                } */
                  timer = setTimeout(()=>{
                    console.log("111")
                    flag = false
                    el.scrollTop = 0
                },1000)
              
            }
           
        })
    }

})


