import { Toast,Indicator } from 'mint-ui';
import axios from 'axios'

export default{
    getData(context){
       //console.log(context.state.page)
        if(context.state.page > 2){
            Toast({
                 message: '没有更多数据',
                position: 'bottom',
                duration: 2000
            });
            return
        }
        context.commit("changeLoading",true)
       Indicator.open({
            text: '加载中...',
            spinnerType: 'double-bounce'
            });
       
        axios.get("/lg/listmore.json",{
            params:{  // params || query
                pageNo:this.page,
                pageSize:10
            }
        }).then((res)=>{
           //console.log(res)
            context.commit("changeList",res.data.content.data.page.result)
            
            context.commit("changeLoading",false)
            context.commit("changePage")
            Indicator.close()
            
        })
    },
}


