<template>
    <div>
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="item in news">
                    <a :href="item.url_https">
                        <img :src="item.thumb" alt="">
                    </a>
                    <p> {{item.fpTime|timeFilter}} </p>
                    </div>      
            </div>
             <div class="swiper-pagination"></div>
        </div>

    </div>
</template>

<script>
import '../../module/filter'
export default {
 data(){
        return {
            news:[],
            allLi:[]
        }
    },
     created() {
        this.$http.get("/wz/api/v3/get?_=1543633856801").then((res)=>{
           // console.log(res)
            this.news = res.data.data.filter((item,index)=>{
                return index < 5
            })
        })
       
    },
     watch:{
        news(n){
            this.$nextTick(()=>{
                 var mySwiper = new Swiper ('.swiper-container', {
                   
                    loop: true, // 循环模式选项
                    
                    // 如果需要分页器
                    pagination: {
                    el: '.swiper-pagination',
                    },
                    autoplay: {
                    delay: 1000,
                    disableOnInteraction: true,
                    }
                    
                   
                })        
            })
        }
    },
}
</script>

<style lang="scss">
    $sc:25;
    .swiper-container,.swiper-wrapper,.swiper-slide{
        width: 100%;
        
    }
    .swiper-slide{
        img{
            width: 100%;
            height: 135/$sc+rem
        }
    }
    .swiper-pagination{
        height: 29/$sc+rem;
        background:rgba(0,0,0,.5);
        bottom:0;
        text-align: right
    }
   
</style>


