import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/page/home'
import Index from '@/components/page/index'
import Video from '@/components/page/video'
import User from '@/components/page/user'
import Subscribe from '@/components/page/subscribe'
import Search from '@/components/page/search'
import Login from '@/components/page/login'
import Register from '@/components/page/register'
import List from '@/components/page/list'
import TypeList from '@/components/page/typeList'
Vue.use(Router)
import Nav from '@/components/page/nav'
import Nav2 from '@/components/page/nav2'
export default new Router({
  routes: [
    {
      path: '/',
     redirect:{
       name:'home'
     }
    },
     {
      path: '/home',
      name: 'home',
      component: Home,
      redirect:{name:"index"},
     
      children:[
        {
          path:'/home/index',
          name:'index',
         //component:Index,
          redirect:{name:"list"},
          components:{
            Nav:Nav,
            default:Index
          },
          children:[
            {
              path:"/home/index/list",
              name:'list',
             component:List,
             
            },
            {
              path:"/home/index/:type",
              name:'typeList',
              component:TypeList
            }
          ]
        },
        {
          path:'/home/video',
          name:'video',
         // component:Video,
           components:{
            Nav:Nav2,
            default:Video
          } 
        },
        {
          path:'/home/subscribe',
          name:'subscribe',
          component:Subscribe
        }

      ]
    },
    {
      path:'/user',
      name:'user',
      component:User
    },
    {
      path:'/search',
      name:'search',
      component:Search
    },
    {
      path:'/login',
      name:'login',
      component:Login
    },
    {
      path:'/register',
      name:'register',
      component:Register
    }
  ]
})



