<template>
    <div class="box">
        <div class="top">
            <router-link to="/user">
                <i class="fa fa-reply"></i>
                <span>退出</span>
            </router-link>
            <span class="right">
                <router-link to="register">注册</router-link>
            </span>
        </div>
        <section>
            <img src="../../../static/img/logo.png" alt="">
            <ul>
                <li>
                    <i class="fa fa-user-o"></i>
                    <input v-model="phone" type="text" placeholder="邮箱/手机号">
                </li>
                <li>
                    <i class="fa fa-unlock-alt"></i>
                    <input v-model="password" type="text" placeholder="密码" />
                </li>
            </ul>
        </section>
        <footer>
            <div class="foo" @click="login">
                登录
            </div>
        </footer>
    </div>
</template>

<script>
export default {
    data(){
        return{
            phone:'',
            password:'',
            list:null
        }
    },
    methods:{
        login(){
            this.$http.get("http://localhost:3000/list").then((res) => {
                //console.log(res.data)
                this.list = res.data.filter((item)=>{
                    //console.log(item.)
                    return item.phone === this.phone
                })
               //console.log(this.list) 
                 if(this.list.length >0){
                  if(this.list[0].password === this.password){
                        alert("登录成功")
                        this.$router.push("/home")
                  }else{
                       alert("密码错误")
                  }
                }else{
                    alert("用户不存在,请先注册")
                }  
            })
            
            
        }
    }
}
</script>

<style lang="scss" scoped>
$sc:25;
.box{
    overflow: hidden;
    font-size: 18px;
    .right{
        float:right;
    }
    .top{
        width: 100%;
         height: 43.98/$sc+rem;
        line-height: 43.98/$sc+rem;
        background: #fff;
        border: 1px solid #ccc;
    }
    section{
        width: 100%;
        text-align: center;
        height: 324/$sc+rem;
        background: #eee;
        img{
            width: 147.484/$sc+rem;
            height: 147.484/$sc+rem;
            padding: 37.5/$sc+rem  0;
        }
        ul{
            width: 100%;
            overflow: hidden;
            li{
               
                text-align: left;
                width: 100%;
                height: 49/$sc+rem;
                border-bottom: 1px solid #ccc;
                background: #fff;
                padding-left: 15/$sc+rem;
                input{
                    height: 100%;
                }
                i{
                    font-size: 26px
                }
            }
        }
    }
    footer{
        width: 100%;
        box-sizing: border-box;
        height: 264/$sc+rem;
        background: #eee;
        padding: 15/$sc+rem 12/$sc+rem 0;
        .foo{
            width: 349/$sc+rem;
            height: 35.5/$sc+rem;
            background: #fff;
            text-align: center;
            line-height: 35.5/$sc+rem;
        }
    }
}

</style>



